import AssemblyTime from "../../../components/content/providerTools/activityFormComps/assemblyTime/AssemblyTime.vue";
import TeachingSyllabus from "../../../components/content/providerTools/activityFormComps/teachingSyllabus/TeachingSyllabus.vue";
import SpecificSchedule from "../../../components/content/providerTools/activityFormComps/specificSchedule/SpecificSchedule.vue";
import TextInputPlusTextarea from "../../../components/content/providerTools/activityFormComps/textInputPlusTextarea/TextInputPlusTextarea.vue";
import InsuranceFinalCost from "../../../components/content/providerTools/activityFormComps/insuranceFinalCost/InsuranceFinalCost.vue";
import LocationSelect from "../../../components/content/providerTools/activityFormComps/locationSelect/LocationSelect.vue";
import BackTop from "../../../components/common/backTop/BackTop.vue";
import VideoUpload from "../../../components/content/providerTools/activityFormComps/videoUpload/VideoUpload.vue";
import Loading from "../../../components/common/loading/Loading.vue";
import EditReasonDialog from "../../../components/content/providerTools/editReasonDialog/EditReasonDialog.vue";
import InsuranceSelect from "../../../components/content/providerTools/activityFormComps/insuranceSelect/InsuranceSelect.vue";
import { warnBling } from "../../../common/utils";
import {
  $getProductByID,
  $getEnvInfo,
  $submitProductUpdateTicket,
  $submitProductToUnderwrite,
} from "../../../network/providerTools/activity";
import uuid from "uuid";

export default {
  name: "e-activityDetail",
  beforeRouteEnter(to, from, next) {
    if (
      window.localStorage.getItem("isProvider") === "true" ||
      window.localStorage.getItem("isStaff") === "true"
    ) {
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010006");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
      window.localStorage.removeItem("providerID");
    }
  },
  async created() {
    this.timeoutLoadingToWait = () => {
      return new Promise((resolve) => {
        setTimeout(() => {
          this.isLoading = false;
          resolve();
        }, 1000);
      });
    };
  },
  async mounted() {
    this.$nextTick(async () => {
      this.isLoading = true;
      try {
        const response1 = await $getEnvInfo();
        console.log(response1);

        if (response1.Code === 200 && response1.msg === "success") {
          const {
            activitymodes,
            ages,
            categories,
            cities,
            days,
            features,
            theme_related,
          } = response1.details;

          // 
          for (let i = 0; i < categories.length; i++) {
            categories[i].selected = false;
          }
          this.categoryList = window.JSON.parse(
            window.JSON.stringify(categories)
          );

          // 
          for (let i = 0; i < features.length; i++) {
            features[i].selected = false;
          }
          this.featureList = window.JSON.parse(window.JSON.stringify(features));

          // 
          for (let i = 0; i < activitymodes.length; i++) {
            activitymodes[i].label = activitymodes[i].name;
            activitymodes[i].value = activitymodes[i].id;
          }
          this.scheduleFormSelects = window.JSON.parse(
            window.JSON.stringify(activitymodes)
          );

          // 
          const tempArr = [];
          for (const index in theme_related) {
            const obj = {
              id: Number(index),
              label: theme_related[index],
              value: theme_related[index],
            };
            tempArr.push(obj);
          }
          this.contentFormSelects = window.JSON.parse(
            window.JSON.stringify(tempArr)
          );

          // 
          const tempArr2 = [];
          for (const index in ages) {
