  methods: {
    changeCategorySelectDialogStatus() {
      this.isShowCategorySelectDialog = !this.isShowCategorySelectDialog;
    },
    changeFeatureSelectDialogStatus() {
      this.isShowFeatureSelectDialog = !this.isShowFeatureSelectDialog;
    },
    hideCategorySelectDialog() {
      this.isShowCategorySelectDialog = false;
    },
    hideFeatureSelectDialog() {
      this.isShowFeatureSelectDialog = false;
    },
    // item
    modifyActivityCategoryList(index) {
      // item
      // falsetrue
      const t = this.categoryList[index].selected;

      // itemtrue
      if (t) {
        const tempObj1 = this.categoryList[index];
        // selectedCategoryListitem
        const tempObj2 = this.selectedCategoryList.find(
          (item) => item.id === tempObj1.id && item.name === tempObj1.name
        );
        // itemselectedCategoryList
        const i = this.selectedCategoryList.indexOf(tempObj2); // 
        // selectedCategoryListitem
        this.selectedCategoryList.splice(i, 1);
      }
      // itemfalse
      else {
        const temp = this.categoryList[index];
        // item
        const obj = {
          id: temp.id,
          name: temp.name,
        };
        // pushselectedCategoryList
        this.selectedCategoryList.push(obj);
      }

      // itemselected
      this.$set(this.categoryList[index], "selected", !t);
    },
    modifyActivityFeatureList(index) {
      const getObjByIndexFromFeatureList = this.featureList[index];
      const t = getObjByIndexFromFeatureList.selected;
      if (t) {
        const findInSelectedFeatureList = this.selectedFeatureList.find(
          (item) =>
            item.id === getObjByIndexFromFeatureList.id &&
            item.name === getObjByIndexFromFeatureList.name
        );
        const i = this.selectedFeatureList.indexOf(findInSelectedFeatureList);
        this.selectedFeatureList.splice(i, 1);
      } else {
        const obj = {
          id: getObjByIndexFromFeatureList.id,
          name: getObjByIndexFromFeatureList.name,
        };
        this.selectedFeatureList.push(obj);
      }

      this.$set(this.featureList[index], "selected", !t);
    },
    deleteSelectedCategoryItem(index) {
      const tempObj = this.selectedCategoryList[index];
      const findObj = this.categoryList.find(
        (item) => item.id === tempObj.id && item.name === tempObj.name
      );
      const i = this.categoryList.indexOf(findObj);
      this.$set(this.categoryList[i], "selected", false);

      this.selectedCategoryList.splice(index, 1);
    },
    deleteSelectedFeatureItem(index) {
      const getObjByIndexFromSelectedFeatureList =
        this.selectedFeatureList[index];
      const findInFeatureList = this.featureList.find(
        (item) =>
          item.id === getObjByIndexFromSelectedFeatureList.id &&
          item.name === getObjByIndexFromSelectedFeatureList.name
      );
      const i = this.featureList.indexOf(findInFeatureList);
      this.$set(this.featureList[i], "selected", false);

      this.selectedFeatureList.splice(index, 1);
    },
    updateCompsOffsetTopWhenPicUpload() {
      setTimeout(() => {
        for (let i = 0; i < this.compList.length; i++) {
          this.$set(
            this.compList[i],
            "offsetTop",
            this.$refs[this.compList[i].referenceName].$el.offsetTop
          );
        }
      }, 550);
    },
    // 
    backToTop() {
      // 
      const backTopInterval = setInterval(function () {
        if (
          document.documentElement.scrollTop > 0 ||
          document.body.scrollTop > 0
        ) {
          document.documentElement.scrollTop -= 76;
          document.body.scrollTop -= 76;
        } else {
          clearInterval(backTopInterval);
        }
      }, 1);
    },
    modifyInvoiceList(index) {
      // item
      // falsetrue
      const t = this.invoiceList[index].selected;

      // itemtrue
      if (t) {
        const tempObj1 = this.invoiceList[index];
        // selectedInvoiceListitem
        const tempObj2 = this.selectedInvoiceList.find(
          (item) => item.id === tempObj1.id && item.name === tempObj1.name
        );
        // itemselectedInvoiceList
        const i = this.selectedInvoiceList.indexOf(tempObj2); // 
        // selectedInvoiceListitem
        this.selectedInvoiceList.splice(i, 1);
      }
      // itemfalse
      else {
        const temp = this.invoiceList[index];
        // item
        const obj = {
          id: temp.id,
          name: temp.name,
        };
        // pushselectedCategoryList
        this.selectedInvoiceList.push(obj);
      }

      // itemselected
      this.$set(this.invoiceList[index], "selected", !t);
    },
    cancelAllSelectedInInvoiceList() {
      for (const item of this.invoiceList) {
        this.$set(item, "selected", false);
      }
      const len = this.selectedInvoiceList.length;
      this.selectedInvoiceList.splice(0, len);
    },
    deleteSelectedInvoiceItem(index) {
      const getObjByIndexFromSelectedInvoiceList =
        this.selectedInvoiceList[index];
      const findInInvoiceList = this.invoiceList.find(
        (item) =>
          item.id === getObjByIndexFromSelectedInvoiceList.id &&
          item.name === getObjByIndexFromSelectedInvoiceList.name
      );
      const i = this.invoiceList.indexOf(findInInvoiceList);
      this.$set(this.invoiceList[i], "selected", false);

      this.selectedInvoiceList.splice(index, 1);
    },

    setHorizontalShowsErrorHint(errorMessage) {
      this.$set(this.compList[36], "errorHint", errorMessage);
    },
    setVerticalShowsErrorHint(errorMessage) {
      this.$set(this.compList[37], "errorHint", errorMessage);
    },

    async showEditReasonDialog() {
      this.isLoading = true;
      await this.timeoutLoadingToWait();

      for (const val of this.compList) {
        if (val.valueLegitimacy === false) {
          document.body.scrollTop = document.body.scrollTop =
            this.$refs[val.referenceName].$el.offsetTop - 160;
          document.documentElement.scrollTop =
            this.$refs[val.referenceName].$el.offsetTop - 160;
          if (val.hasOwnProperty("errorHint")) {
            this.$toast.error(val.errorHint);
          }
          await warnBling(this.$refs[val.referenceName].$el);
          return;
        }
      }

      if (!this.updateMark) {
        this.$toast.warn("");
        return;
      }

      this.ifShowEditReasonDialog = true;
    },
    cancelAllSelectedInCategoryList() {
      for (const item of this.categoryList) {
        this.$set(item, "selected", false);
      }
      const len = this.selectedCategoryList.length;
      this.selectedCategoryList.splice(0, len);
    },
    cancelAllSelectedInFeatureList() {
      for (const item of this.featureList) {
        this.$set(item, "selected", false);
      }
      const len = this.selectedFeatureList.length;
      this.selectedFeatureList.splice(0, len);
    },

    // 
    async submitModifiedActivity(createReason) {
      if (createReason.trim() === "") {
        this.$toast.error("");
        return;
      }

      const updateData = {};
      // 0-5
      for (let index = 0; index <= 5; index++) {
        if (this.compList[index].updateStatus) {
          if (
            typeof this.$refs[this.compList[index].referenceName].$data
              .targetData === "object"
          ) {
            updateData[this.compList[index].referenceName] = window.JSON.parse(
              window.JSON.stringify(
                this.$refs[this.compList[index].referenceName].$data.targetData
              )
            );
          } else {
            updateData[this.compList[index].referenceName] =
              this.$refs[this.compList[index].referenceName].$data.targetData;
          }
        }
      }

      if (this.compList[6].updateStatus) {
        const processedSelectedInvoiceList = [];
        for (const item of this.selectedInvoiceList) {
          processedSelectedInvoiceList.push(item.name);
        }
        updateData.invoice = processedSelectedInvoiceList;
      }

      if (this.compList[68].updateStatus) {
        // 
        const copyArr = window.JSON.parse(
          window.JSON.stringify(
            this.$refs[this.compList[68].referenceName].$data.monitoredMixedObj
          )
        );
        if (copyArr[2].insured_amount !== "") {
          copyArr[2].insured_amount = Number(copyArr[2].insured_amount);
        }
        if (copyArr[3].insured_amount !== "") {
          copyArr[3].insured_amount = Number(copyArr[3].insured_amount);
        }
        const insuranceTargetData = [];
        for (const item of copyArr) {
          if (item.name !== "" && item.insured_amount !== "") {
            insuranceTargetData.push(item);
          }
        }
        updateData.insurance = insuranceTargetData;
      }

      // 7-12
      for (let index = 7; index <= 12; index++) {
        if (this.compList[index].updateStatus) {
          if (
            typeof this.$refs[this.compList[index].referenceName].$data
              .targetData === "object"
          ) {
            // Observer
            updateData[this.compList[index].referenceName] = window.JSON.parse(
              window.JSON.stringify(
                this.$refs[this.compList[index].referenceName].$data.targetData
              )
            );
          } else {
            updateData[this.compList[index].referenceName] =
              this.$refs[this.compList[index].referenceName].$data.targetData;
          }
        }
      }

      // 13,14
      if (this.compList[13].updateStatus) {
        const processedSelectedCategoryList = [];
        for (const item of this.selectedCategoryList) {
          processedSelectedCategoryList.push(item.id);
        }
        updateData.categories = processedSelectedCategoryList;
      }

      if (this.compList[14].updateStatus) {
        const processedSelectedFeatureList = [];
        for (const item of this.selectedFeatureList) {
          processedSelectedFeatureList.push(item.id);
        }
        updateData.features = processedSelectedFeatureList;
      }

      // 15-20
      for (let index = 15; index <= 20; index++) {
        if (this.compList[index].updateStatus) {
          if (
            typeof this.$refs[this.compList[index].referenceName].$data
              .targetData === "object"
          ) {
            updateData[this.compList[index].referenceName] = window.JSON.parse(
              window.JSON.stringify(
                this.$refs[this.compList[index].referenceName].$data.targetData
              )
            );
          } else {
            updateData[this.compList[index].referenceName] =
              this.$refs[this.compList[index].referenceName].$data.targetData;
          }
        }
      }

      // 21
      if (this.compList[21].updateStatus) {
        updateData.location_province =
          this.$refs[
            this.compList[21].referenceName
          ].$data.monitoredMixedObj.provinceName;
        updateData.location_city =
          this.$refs[
            this.compList[21].referenceName
          ].$data.monitoredMixedObj.cityName;
        updateData.location_district =
          this.$refs[
            this.compList[21].referenceName
          ].$data.monitoredMixedObj.countyName;
        updateData.location_street =
          this.$refs[
            this.compList[21].referenceName
          ].$data.monitoredMixedObj.detailAddress;
      }

      // 22-38
      for (let index = 22; index <= 38; index++) {
        if (this.compList[index].updateStatus) {
          if (index !== 36 && index !== 37) {
            if (
              typeof this.$refs[this.compList[index].referenceName].$data
                .targetData === "object"
            ) {
              updateData[this.compList[index].referenceName] =
                window.JSON.parse(
                  window.JSON.stringify(
                    this.$refs[this.compList[index].referenceName].$data
                      .targetData
                  )
                );
            } else {
              updateData[this.compList[index].referenceName] =
                this.$refs[this.compList[index].referenceName].$data.targetData;
            }
          } else {
            const tempArr = [];
            for (const item of this.$refs[this.compList[index].referenceName]
              .$data.pictureList) {
              const tempItem = {};
              tempItem.name = item.name;
              tempItem.url = item.url;
              tempArr.push(tempItem);
            }
            updateData[this.compList[index].referenceName] = tempArr;
          }
        }
      }

      // 39-55
      let prepare_instruction_update_flag = false;
      for (let index = 39; index < 55; index++) {
        if (this.compList[index].updateStatus) {
          prepare_instruction_update_flag = true;
          break;
        }
      }

      if (prepare_instruction_update_flag) {
        const campInstruction = [];
        for (let index = 39; index <= 55; index++) {
          if (
            typeof this.$refs[this.compList[index].referenceName].$data
              .targetData === "object"
          ) {
            const obj = {
              name: this.compList[index].chineseName,
              content: window.JSON.parse(
                window.JSON.stringify(
                  this.$refs[this.compList[index].referenceName].$data
                    .targetData
                )
              ),
            };
            campInstruction.push(obj);
          } else {
            const obj = {
              name: this.compList[index].chineseName,
              content:
                this.$refs[this.compList[index].referenceName].$data.targetData,
            };
            campInstruction.push(obj);
          }
        }
        updateData.prepare_instruction = campInstruction;
      }

      // 56-67
      for (let index = 55; index <= 67; index++) {
        if (this.compList[index].updateStatus) {
          if (index !== 60 && index !== 61) {
            if (
              typeof this.$refs[this.compList[index].referenceName].$data
                .targetData === "object"
            ) {
              updateData[this.compList[index].referenceName] =
                window.JSON.parse(
                  window.JSON.stringify(
                    this.$refs[this.compList[index].referenceName].$data
                      .targetData
                  )
                );
            } else {
              updateData[this.compList[index].referenceName] =
                this.$refs[this.compList[index].referenceName].$data.targetData;
            }
          } else if (index === 60) {
            updateData.advantage1 =
              this.$refs[
                this.compList[index].referenceName
              ].$data.targetData.title;
            updateData.advantage1_intro = window.JSON.parse(
              window.JSON.stringify(
                this.$refs[this.compList[index].referenceName].$data.targetData
                  .content
              )
            );
          } else {
            updateData.advantage2 =
              this.$refs[
                this.compList[index].referenceName
              ].$data.targetData.title;
            updateData.advantage2_intro = window.JSON.parse(
              window.JSON.stringify(
                this.$refs[this.compList[index].referenceName].$data.targetData
                  .content
              )
            );
          }
        }
      }

      updateData.id = this.product_id;

      this.$refs.editReasonDialog.ifShowLoadingGif = true;
      // 
      try {
        const response = await $submitProductUpdateTicket(
          createReason,
          updateData
        );
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("", 0, 330);
          this.resetCompsData(); // updateMarkfalse
          //this.$router.go(-1);
        } else {
          this.$toast.error("");
        }
      } catch (err) {
        this.$toast.error("");
      }
      this.$refs.editReasonDialog.ifShowLoadingGif = false;
      this.ifShowEditReasonDialog = false;
    },
    // 
    handleScroll() {
      const scrollTop =
        document.documentElement.scrollTop || document.body.scrollTop; // 
      this.isShowBackTop = scrollTop >= 750 ? true : false;
      this.isShowSubstituteButtonZone =
        scrollTop >= this.originButtonZoneOffsetTop ? true : false;
    },
    beforeUnloadHandler(e) {
      if (this.updateMark) {
        e.returnValue = "?";
      } else {
        return;
      }
    },
    resetCompsData() {
      const data = window.JSON.parse(
        window.sessionStorage.getItem("productForm")
      );
      this.autoWriteForm(data);
    },
    async checkIfHaveUpdate() {
      this.isLoading = true;
      await this.timeoutLoadingToWait();

      if (this.updateMark) {
        this.modalVisible1 = true;
        this.modalText = "";
      } else {
        this.modalVisible2 = true;
        this.modalText = "";
      }
    },
    showSecondModal() {
      this.resetCompsData();
      this.modalVisible1 = false;
      this.modalVisible2 = true;
      this.modalText = "";
    },
    doNothingButHideModal() {
      this.modalVisible1 = false;
    },
    doNothingButHideModal2() {
      this.modalVisible2 = false;
    },
    async applyInsurance() {
      this.confirmLoading = true;
      const requestPayload = {
        id: this.product_id,
      };

      // 
      try {
        const response = await $submitProductToUnderwrite(requestPayload);
        this.confirmLoading = false;
        this.modalVisible2 = false;
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          this.$toast.success("", 0, 330);
          this.$router.replace("/tools/provider/activity/update");
        } else {
          this.$toast.error("");
        }
      } catch (err) {
        this.confirmLoading = false;
        this.modalVisible2 = false;
        this.$toast.error("");
      }
    },
  },
  watch: {
    isShowInvoiceSelectDialog(newVal) {
      if (newVal) {
        this.$refs.clickToSelectInvoice.showText = "";
        this.isShowCategorySelectDialog = false;
        this.isShowFeatureSelectDialog = false;
      } else {
        this.$refs.clickToSelectInvoice.showText = "";
      }
    },
    isShowCategorySelectDialog(newVal) {
      if (newVal) {
        this.$refs.clickToSelectCategory.showText = "";
        this.isShowFeatureSelectDialog = false;
        this.isShowInvoiceSelectDialog = false;
      } else {
        this.$refs.clickToSelectCategory.showText = "";
      }
    },
    isShowFeatureSelectDialog(newVal) {
      if (newVal) {
        this.$refs.clickToSelectFeature.showText = "";
        this.isShowCategorySelectDialog = false;
        this.isShowInvoiceSelectDialog = false;
      } else {
        this.$refs.clickToSelectFeature.showText = "";
      }
    },

    selectedInvoiceList: {
      handler(newVal) {
        if (newVal.length !== 0) {
          this.$set(this.compList[6], "valueLegitimacy", true);
        } else {
          this.$set(this.compList[6], "valueLegitimacy", false);
        }

        // 
        if (this.invoiceSelectCompStatus === "101") {
          // 
          if (newVal.length !== this.originalSelectedInvoiceList.length) {
            this.$set(this.compList[6], "updateStatus", true);
            this.notEdited = false;
          } else {
            let flag = true; // newValthis.originalSelectedCategoryList
            for (const val of newVal) {
              const one = this.originalSelectedInvoiceList.find(
                (item) => item.name === val.name
              );
              if (!one) {
                flag = false;
              }
            }
            if (flag) {
              this.$set(this.compList[6], "updateStatus", false);
            } else {
              this.$set(this.compList[6], "updateStatus", true);
              this.notEdited = false;
            }
          }
        }
      },
      deep: true,
    },
    selectedCategoryList: {
      handler(newVal) {
        // console.log(newVal)
        // setTimeout(() => {
        //   this.notEdited = false
        // }, 5000);
        if (newVal.length !== 0) {
          this.$set(this.compList[13], "valueLegitimacy", true);
        } else {
          this.$set(this.compList[13], "valueLegitimacy", false);
        }

        // 
        if (this.categorySelectCompStatus === "101") {
          // 
          if (newVal.length !== this.originalSelectedCategoryList.length) {
            this.$set(this.compList[13], "updateStatus", true);
            this.notEdited = false;
          } else {
            let flag = true; // newValthis.originalSelectedCategoryList
            for (const val of newVal) {
              const one = this.originalSelectedCategoryList.find(
                (item) => item.name === val.name
              );
              if (!one) {
                flag = false;
                return;
              }
            }
            if (flag) {
              this.$set(this.compList[13], "updateStatus", false);
            } else {
              this.$set(this.compList[13], "updateStatus", true);
              this.notEdited = false;
            }
          }
        }
      },
      deep: true,
    },
    selectedFeatureList: {
      handler(newVal) {
        if (newVal.length !== 0) {
          this.$set(this.compList[14], "valueLegitimacy", true);
        } else {
          this.$set(this.compList[14], "valueLegitimacy", false);
        }

        // 
        if (this.featureSelectCompStatus === "101") {
          // 
          if (newVal.length !== this.originalSelectedFeatureList.length) {
            this.$set(this.compList[14], "updateStatus", true);
            this.notEdited = false;
          } else {
            let flag = true; // newValthis.originalSelectedCategoryList
            for (const val of newVal) {
              const one = this.originalSelectedFeatureList.find(
                (item) => item.name === val.name
              );
              if (!one) {
                flag = false;
                return;
              }
            }
            if (flag) {
              this.$set(this.compList[14], "updateStatus", false);
            } else {
              this.$set(this.compList[14], "updateStatus", true);
              this.notEdited = false;
            }
          }
        }
      },
      deep: true,
    },
    compList: {
      handler(newVal) {
        let flag = false;
        for (const val of newVal) {
          if (val.updateStatus === true) {
            flag = true;
          }
        }
        if (flag) {
          this.updateMark = true;
        } else {
          this.updateMark = false;
        }
      },
      deep: true,
    },
  },
  components: {
    GeneralFormTextInput,
    ItemSelectDialog,
    GeneralFormSelect,
    DatePicker,
    RadioSingleSelect,
    TextareaZone,
    RefundInstruction,
